<template>
  <div class="sidebar">
    <q-tabs v-model="defaultExpandedNames">
      <q-tab-pane
        v-for="(item, index) in assets.menu"
        :key="index"
        :name="String(index)"
        :label="item.title"
        :disabled="item.type !== 'map' && !hasMap ? true : false"
      >
        <div
          v-for="component in item.components"
          :key="component.componentMeta.name"
          class="grid-stack-item component-item"
          :gs-w="component.componentMeta.w"
          :gs-h="component.componentMeta.h"
        >
          <!--  -->
          <span class="grid-stack-item-content"
            >{{ component.componentMeta.type }}-{{
              component.componentName
            }}</span
          >
          <span class="item-img">
            <img
              class="component-img"
              :title="component.componentName"
              :src="
                require(`@/designer/assets/images/${component.componentMeta.imgName}.png`)
              "
              alt=""
            />
            <div class="item-title">{{ component.componentMeta.title }}</div>
          </span>
        </div>
      </q-tab-pane>
    </q-tabs>
  </div>
</template>



<script >
import { useWorkbench, useDragon, useDocument } from '@/designer/hooks';
import { computed, ref } from '@vue/composition-api';
const __sfc_main = {
  name: 'NavMenu'
};
__sfc_main.setup = (__props, __ctx) => {
  const workbench = useWorkbench();
  const dragon = useDragon();
  const document = useDocument();
  const assets = computed(() => {
    return workbench.value.assets;
  });
  const hasMap = computed(() => {
    return document.value.map.value.props.mapId;
  });
  const defaultExpandedNames = ref(0);
  return {
    assets,
    hasMap,
    defaultExpandedNames
  };
};
export default __sfc_main;
</script>

